import {TweenLite, TimelineLite, TweenMax, Linear} from 'gsap'
import 'gsap/CSSPlugin'

export const createRotateAnimation = ({target, duration, rotate}) => {
  return new Promise(resolve => {
    const animation = TweenLite.to(target, duration, {
      rotation: rotate,
      transformOrigin: 'center',
      ease: Linear.easeNone,
      onComplete: () => resolve(animation)
    })
  })
}

export const createCircleAnimation = ({target, duration}) => {
  const timelineLite = new TimelineLite()

  return new Promise(resolve => {
    const animation = timelineLite
      .to(target, duration / 2, {scale: 1.1})
      .to(target, duration / 2, {
        scale: 1,
        onComplete: () => resolve(animation)
      })
  })
}

export const createFullPageAnimation = ({target, duration, scale}) => {
  return new Promise(resolve => {
    const animation = TweenLite
      .to(target, duration / 2, {
        scale,
        onComplete: () => resolve(animation)
      })
  })
}

export const createFadeInAnimation = ({target, duration, from, to, save}) => {
  return new Promise(resolve => {
    const animation = TweenLite.fromTo(target, duration, {
      ...from
    }, {
      ...to,
      onComplete: () => resolve(animation)
    })
    save(animation)
  })
}

export const createMoveInAnimation = ({target, duration, from, to, save}) => {
  return new Promise(resolve => {
    const animation = TweenLite.fromTo(target, duration, {
      ...from
    }, {
      ...to,
      onComplete: () => resolve(animation)
    })
    save(animation)
  })
}

export const createSamePositionRepeatAnimation = ({target, duration, otherProps, save}) => {
  const animation = TweenMax
    .to(target, duration, {
      ...otherProps,
      repeat: -1
    })

  save(animation)
  return animation
}

export const createMoveToAnimation = ({target, duration, to, save}) => {
  return new Promise(resolve => {
    const animation = TweenLite.to(target, duration, {
      ...to,
      onComplete: () => resolve(animation)
    })

    save(animation)
  })
}



