import React, {Component} from 'react'
import { withStyles } from '@material-ui/core/styles'
import {Linear} from 'gsap'
import {SIZE, GRAPH_BASE_SIZE} from '../../../constant'
import {createAngle} from '../../../util/drawCanvasUtil'
import {
  createFadeInAnimation,
  createMoveInAnimation,
  createMoveToAnimation
} from '../../../util/animationUtil'

import blue from '@material-ui/core/colors/blue'

const DURACTION = {
  FIRST_TRANSITION: 1,
  SECOND_TRANSITION: 1.5,
  THIRD_TRANSITION: 10
}

const GRAPH_SIZE = {
  ANGLE: GRAPH_BASE_SIZE * 0.15
}

const POSITION = {
  BOTTOM_ANGLE: SIZE.FULL_PAGE.HEIGHT - GRAPH_SIZE.ANGLE * 1.6
}

const styles = () => ({
  root: {
    position: 'relative',
    width: '100vw',
    height: '100vh',
    backgroundColor: blue[700],
    overflow: 'hidden'
  },
  messageArea: {
    position: 'fixed',
    top: 'calc(40vh)',
    left: 'calc(50vw - 6rem)'
  },
  firstMessage: {
    color: '#fff',
    textAlign: 'center',
    fontWeight: 'bold',
    display: 'inline-block',
    width: 'auto'
  },
  result: {
    fontSize: '3.5rem'
  },
  placeholder: {
    transition: 'all .75s',
    fontSize: '1.2rem',
    display: 'inline-block',
    width: '79%',
    textAlign: 'left'
  },
  lastPlaceholder: {
    width: '100%'
  },
  canvas: {
    position: 'fixed'
  },
  optionArea: {
    margin: '1rem 0 1rem 3.5rem',
    color: blue[900],
    fontSize: '2.5rem',
    fontWeight: 'bold',
    textAlign: 'left'
  },
  discription: {
    color: '#fff',
    margin: '2rem 0',
    fontSize: '1.3rem',
    textAlign: 'left'
  },
  fullResult: {
    width: '40%',
    textAlign: 'center',
    position: 'absolute',
    left: '50vw',
    top: '34.7vh'
  },
  tryAgainBtn: {
    transition: 'all .3s',
    display: 'inline-block',
    width: '70%',
    textAlign: 'center',
    color: '#fff',
    padding: '.5rem',
    border: '2px solid #fff',
    borderRadius: '2rem',
    marginBottom: '.5rem',
    cursor: 'pointer',
    '&:hover': {
      backgroundColor: '#fff',
      color: blue[900]
    }
  }
})

class Result extends Component {
  constructor () {
    super()
    this.state = {
      infiniteAnimationList: [],
      isLastPart: false,
      showFullResult: false,
      topAngleList: [
        {
          target: 'oneTime',
          color: '#000',
          first: {
            from: {
              top: 0,
              left: SIZE.FULL_PAGE.WIDTH * 0.07
            },
            to: {
              top: SIZE.FULL_PAGE.HEIGHT * 0.42,
              left: SIZE.FULL_PAGE.WIDTH * 0.07
            }
          },
          second: {
            to: {top: GRAPH_SIZE.ANGLE * -1}
          }
        },
        {
          target: 'oneTime',
          color: blue[900],
          first: {
            from: {
              top: 0,
              left: SIZE.FULL_PAGE.WIDTH * 0.3
            },
            to: {
              top: SIZE.FULL_PAGE.HEIGHT * 0.25,
              left: SIZE.FULL_PAGE.WIDTH * 0.3
            }
          },
          second: {
            to: {top: GRAPH_SIZE.ANGLE * -1}
          }
        },
        {
          target: 'oneTime',
          color: '#000',
          first: {
            from: {
              top: 0,
              left: SIZE.FULL_PAGE.WIDTH * 0.75
            },
            to: {
              top: SIZE.FULL_PAGE.HEIGHT * 0.45,
              left: SIZE.FULL_PAGE.WIDTH * 0.75
            }
          },
          second: {
            to: {top: GRAPH_SIZE.ANGLE * -1}
          }
        },
        {
          target: 'reuse',
          color: blue[900],
          first: {
            from: {
              top: 0,
              left: SIZE.FULL_PAGE.WIDTH * 0.22
            },
            to: {
              top: SIZE.FULL_PAGE.HEIGHT * 0.55,
              left: SIZE.FULL_PAGE.WIDTH * 0.22
            }
          },
          second: {
            to: {
              top: SIZE.FULL_PAGE.HEIGHT * 0.461,
              left: SIZE.FULL_PAGE.WIDTH * 0.5
            }
          }
        },
        {
          target: 'reuse',
          color: blue[900],
          first: {
            from: {
              top: 0,
              left: SIZE.FULL_PAGE.WIDTH * 0.65
            },
            to: {
              top: SIZE.FULL_PAGE.HEIGHT * 0.05,
              left: SIZE.FULL_PAGE.WIDTH * 0.65
            }
          },
          second: {
            to: {
              top: SIZE.FULL_PAGE.HEIGHT * 0.38,
              left: SIZE.FULL_PAGE.WIDTH * 0.5
            }
          }
        },
        {
          target: 'reuse',
          color: blue[900],
          first: {
            from: {
              top: 0,
              left: SIZE.FULL_PAGE.WIDTH * 0.92
            },
            to: {
              top: SIZE.FULL_PAGE.HEIGHT * 0.3,
              left: SIZE.FULL_PAGE.WIDTH * 0.92
            }
          },
          second: {
            to: {
              top: SIZE.FULL_PAGE.HEIGHT * 0.54,
              left: SIZE.FULL_PAGE.WIDTH * 0.5
            }
          }
        }
      ],
      bottomAngleList: [
        {
          color: 'black',
          config: {
            color: '#000'
          },
          first: {
            from: {
              top: SIZE.FULL_PAGE.HEIGHT,
              left: SIZE.FULL_PAGE.WIDTH / 2
            },
            to: {
              top: POSITION.BOTTOM_ANGLE
            }
          },
          second: {
            to: {
              rotation: 80,
              top: SIZE.FULL_PAGE.HEIGHT * 0.25,
              left: SIZE.FULL_PAGE.WIDTH * 0.25 - GRAPH_SIZE.ANGLE * 0.8
            }
          },
          third: {
            rotation: 260
          }
        },
        {
          color: 'white',
          config: {
            color: '#fff',
            style: 'stroke'
          },
          first: {
            from: {
              top: SIZE.FULL_PAGE.HEIGHT,
              left: SIZE.FULL_PAGE.WIDTH / 2 - GRAPH_SIZE.ANGLE / 2
            },
            to: {
              top: POSITION.BOTTOM_ANGLE
            }
          },
          second: {
            to: {
              rotation: 80,
              top: SIZE.FULL_PAGE.HEIGHT * 0.35,
              left: SIZE.FULL_PAGE.WIDTH * 0.25 - GRAPH_SIZE.ANGLE * 0.8
            }
          },
          third: {
            rotation: -210
          }
        },
        {
          color: 'blue',
          config: {
            color: blue[900]
          },
          first: {
            from: {
              top: SIZE.FULL_PAGE.HEIGHT,
              left: SIZE.FULL_PAGE.WIDTH / 2 - GRAPH_SIZE.ANGLE
            },
            to: {
              top: POSITION.BOTTOM_ANGLE
            }
          },
          second: {
            to: {
              top: SIZE.FULL_PAGE.HEIGHT * 0.45,
              left: SIZE.FULL_PAGE.WIDTH * 0.25 - GRAPH_SIZE.ANGLE * 0.8
            }
          },
          third: {
            bezier: {
              curviness: 1.5,
              values: [
                {top: SIZE.FULL_PAGE.HEIGHT * 0.45},
                {top: SIZE.FULL_PAGE.HEIGHT * 0.49},
                {top: SIZE.FULL_PAGE.HEIGHT * 0.45}
              ]
            }
          }
        }
      ]
    }
  }

  componentDidMount () {
    const {topAngleList, bottomAngleList} = this.state
    const {firstMessage} = this.refs

    const messageAnimation = createFadeInAnimation({
      target: firstMessage,
      duration: DURACTION.FIRST_TRANSITION,
      from: {opacity: 0},
      to: {opacity: 1},
      save: this.saveAnimationToState
    })

    const topAngleAnimationList = topAngleList.map((info, index) => {
      const {target, color, first} = info

      const angle = createAngle({
        color,
        ref: this.refs[`${target}${index}`],
        size: GRAPH_SIZE.ANGLE * 0.3
      })

      first.from.rotation = 180
      return createMoveInAnimation({
        target: angle,
        duration: DURACTION.FIRST_TRANSITION,
        ...first,
        save: this.saveAnimationToState
      })
    })

    const bottomAngleAnimationList = bottomAngleList.map(info => {
      const {color, config, first} = info

      const angle = createAngle({
        ref: this.refs[`${color}Angle`],
        size: GRAPH_SIZE.ANGLE * 1.6,
        style: config.style,
        color: config.color
      })

      return createMoveInAnimation({
        target: angle,
        duration: DURACTION.FIRST_TRANSITION,
        ...first,
        save: this.saveAnimationToState
      })
    })

    Promise.all([
      messageAnimation,
      ...topAngleAnimationList,
      ...bottomAngleAnimationList
    ])
      .then(() => {
        return new Promise(resolve => setTimeout(() => resolve(true), 500))
      })
      .then(() => {
        const messageAnimation = createMoveToAnimation({
          target: firstMessage,
          duration: DURACTION.SECOND_TRANSITION / 2,
          to: {
            top: SIZE.FULL_PAGE.HEIGHT * 0.2,
            left: SIZE.FULL_PAGE.WIDTH * 0.5
          },
          save: this.saveAnimationToState
        }).then(() => {
          this.setState({showFullResult: true})
          const {fullResult} = this.refs

          return createFadeInAnimation({
            target: fullResult,
            duration: DURACTION.SECOND_TRANSITION / 2,
            from: {opacity: 0},
            to: {opacity: 1},
            save: this.saveAnimationToState
          })
        })

        const topAngleAnimationList = topAngleList.map((info, index) => {
          const {target, second} = info

          return createMoveToAnimation({
            target: this.refs[`${target}${index}`],
            duration: DURACTION.SECOND_TRANSITION,
            ...second,
            save: this.saveAnimationToState
          })
        })

        const bottomAngleAnimationList = bottomAngleList.map(info => {
          const {color, second} = info

          return createMoveToAnimation({
            target: this.refs[`${color}Angle`],
            duration: DURACTION.SECOND_TRANSITION,
            ...second,
            save: this.saveAnimationToState
          })
        })

        this.setState({isLastPart: true})
        return Promise.all([
          messageAnimation,
          ...topAngleAnimationList,
          ...bottomAngleAnimationList
        ])
      })
      .then(() => {
        const bottomAngleAnimationList = bottomAngleList.map(info => {
          const {color, third} = info

          return createMoveToAnimation({
            target: this.refs[`${color}Angle`],
            duration: DURACTION.THIRD_TRANSITION,
            to: {
              ...third,
              transformOrigin: 'center center',
              ease: Linear.easeNone
            },
            save: this.saveAnimationToState
          })
        })

        return Promise.all([
          ...bottomAngleAnimationList
        ])
      })
  }

  saveAnimationToState = (animation) => {
    this.setState({infiniteAnimationList: [
      ...this.state.infiniteAnimationList,
      animation
    ]})
  }

  render () {
    const {classes, tryAgain} = this.props
    const {topAngleList, bottomAngleList, isLastPart, showFullResult} = this.state

    return (
      <section className={classes.root}>
        {
          topAngleList.map((info, index) => (
            <canvas
              key={index}
              className={classes.canvas}
              ref={`${info.target}${index}`}
              width={GRAPH_SIZE.ANGLE * 0.3}
              height={GRAPH_SIZE.ANGLE * 0.3} />
          ))
        }
        {
          bottomAngleList.map((info, index) => (
            <canvas
              key={index}
              className={classes.canvas}
              ref={`${info.color}Angle`}
              width={GRAPH_SIZE.ANGLE * 1.6}
              height={GRAPH_SIZE.ANGLE * 1.6} />
          ))
        }
        <div ref='firstMessage' className={classes.messageArea}>
          <div className={classes.firstMessage}>
            <p className={`${classes.placeholder} ${isLastPart ? classes.lastPlaceholder : ''}`}>
              Your personality type is
            </p>
            <p className={classes.result}>TRIANGLE</p>
          </div>
        </div>
        {
          showFullResult &&
          <div ref='fullResult' className={classes.fullResult}>
            <div className={classes.optionArea}>
              <p>AGGRESSIVE</p>
              <p>INDEPENDENT</p>
              <p>SELF-MOTIVATED</p>
            </div>
            <p className={classes.discription}>
              The triangle type people are natural-born leaders, full of passion and motivation,  trying to fix their mistakes as they go.
            </p>
            <p className={classes.tryAgainBtn} onClick={tryAgain}>TRY AGAIN</p>
          </div>
        }
      </section>
    )
  }
}

export default withStyles(styles)(Result)



