import React, { Component } from 'react'
import { connect } from 'react-redux'
import * as actionCreators from '../store/actions'

import ParallaxContent from '../component/Parallax/ParallaxContent'

class Parallax extends Component {
  // componentDidMount () {
  //   const {setDataLoaded, getGithubInfo} = this.props

  //   setDataLoaded(false)
  //   getGithubInfo()
  // }

  render () {
    return (
      <main>
        <ParallaxContent {...this.props} />
      </main>
    )
  }
}

const mapStateToProps = state => {
  return {
    userInfo: state.userInfo,
    isDataLoaded: state.isDataLoaded
  }
}

const mapDispatchToProps = dispatch => {
  return {
    getGithubInfo: () => dispatch(actionCreators.getGithubInfo()),
    setDataLoaded: (isDateLoaded) => dispatch(actionCreators.setDataLoaded(isDateLoaded))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(Parallax)



