import React, {Component} from 'react'
import { withStyles } from '@material-ui/core/styles'
import {SIZE, GRAPH_BASE_SIZE} from '../../../constant'
import {
  createStar,
  createCircle,
  createSquare
} from '../../../util/drawCanvasUtil'
import {
  createRotateAnimation,
  createCircleAnimation,
  createFullPageAnimation
} from '../../../util/animationUtil'

import blue from '@material-ui/core/colors/blue'
import pink from '@material-ui/core/colors/pink'

const DURATION = 5
const END_DELAY = 200

const GRAPH_SIZE = {
  SQUARE: GRAPH_BASE_SIZE * 0.3,
  CIRCLE: GRAPH_BASE_SIZE * 0.35,
  STAR: GRAPH_BASE_SIZE * 0.3
}

const styles = () => ({
  root: {
    position: 'relative',
    width: '100vw',
    height: '100vh',
    transition: 'all 1s',
    backgroundColor: blue[700],
    overflow: 'hidden'
  },
  square: {
    position: 'absolute',
    top: `calc(50vh - ${GRAPH_SIZE.SQUARE / 2}px)`,
    left: `calc(50vw - ${GRAPH_SIZE.SQUARE / 2}px)`
  },
  circle: {
    position: 'absolute',
    top: `calc(50vh - ${GRAPH_SIZE.CIRCLE / 2}px)`,
    left: `calc(50vw - ${GRAPH_SIZE.CIRCLE / 2}px)`
  },
  star: {
    position: 'absolute',
    top: `calc(50vh - ${GRAPH_SIZE.STAR / 2}px)`,
    left: `calc(50vw - ${GRAPH_SIZE.STAR / 2}px)`
  },
  wordArea: {
    position: 'absolute',
    top: 'calc(44vh)',
    color: '#fff',
    textAlign: 'center',
    width: '100%',
    fontWeight: 'bold'
  },
  title: {
    fontSize: '2.5rem'
  },
  subTitle: {
    fontSize: '1.1rem'
  }
})

class Homepage extends Component {
  constructor () {
    super()
    this.state = {
      hideFullContent: false,
      backgroundColor: blue[700]
    }
  }

  componentDidMount () {
    const {updatePage} = this.props
    const {square, circle, star} = this.refs

    createSquare({
      ref: square,
      size: GRAPH_SIZE.SQUARE,
      color: '#000'
    })
    createCircle({
      ref: circle,
      size: GRAPH_SIZE.CIRCLE,
      color: blue[900]
    })
    createStar({
      ref: star,
      size: GRAPH_SIZE.STAR,
      color: pink[500]
    })
    let squareAnimation = createRotateAnimation({
      target: square,
      duration: DURATION,
      rotate: -360
    })
    let circleAnimation = createCircleAnimation({
      target: circle,
      duration: DURATION
    })
    let starAnimation = createRotateAnimation({
      target: star,
      duration: DURATION,
      rotate: 360
    })

    Promise.all([
      squareAnimation,
      circleAnimation,
      starAnimation
    ])
      .then(resList => {
        this.setState({backgroundColor: pink[500]})
        squareAnimation = createFullPageAnimation({
          target: square,
          duration: 0.5,
          scale: SIZE.FULL_PAGE.WIDTH / GRAPH_SIZE.SQUARE
        })
        return new Promise(resolve => setTimeout(() => resolve(true), END_DELAY))
      })
      .then(() => {
        circleAnimation = createFullPageAnimation({
          target: circle,
          duration: 0.5,
          scale: SIZE.FULL_PAGE.WIDTH / GRAPH_SIZE.CIRCLE * 1.5
        })
        return new Promise(resolve => setTimeout(() => resolve(true), END_DELAY))
      })
      .then(() => {
        return new Promise(resolve => {
          starAnimation = createFullPageAnimation({
            target: star,
            duration: 0.5,
            scale: SIZE.FULL_PAGE.WIDTH / GRAPH_SIZE.STAR * 2
          })
          resolve(starAnimation)
        })
      })
      .then(() => {
        this.setState({
          backgroundColor: blue[700],
          hideFullContent: true
        })
        setTimeout(() => updatePage({homepage: true}, 'question1'), 1000)
      })
  }

  render () {
    const {hideFullContent, backgroundColor} = this.state
    const {classes} = this.props
    return (
      <section className={classes.root} style={{backgroundColor}}>
        {
          !hideFullContent &&
          <div>
            <canvas className={classes.square} ref='square' width={GRAPH_SIZE.SQUARE} height={GRAPH_SIZE.SQUARE} />
            <canvas className={classes.circle} ref='circle' width={GRAPH_SIZE.CIRCLE} height={GRAPH_SIZE.CIRCLE} />
            <canvas className={classes.star} ref='star' width={GRAPH_SIZE.STAR} height={GRAPH_SIZE.STAR} />
            <div className={classes.wordArea}>
              <p className={classes.title}>GEOMETRY PERSON</p>
              <p className={classes.subTitle}>
                find your core personality type in shapes!
              </p>
            </div>
          </div>
        }
      </section>
    )
  }
}

export default withStyles(styles)(Homepage)



