import React, {Component} from 'react'
import { withStyles } from '@material-ui/core/styles'
import {TimelineMax, TweenLite} from 'gsap'
import {SIZE, GRAPH_BASE_SIZE} from '../../../constant'
import {createSquare} from '../../../util/drawCanvasUtil'

import blue from '@material-ui/core/colors/blue'
import pink from '@material-ui/core/colors/pink'

const GRAPH_SIZE = {
  SQUARE: GRAPH_BASE_SIZE * 0.15
}

const styles = () => ({
  root: {
    position: 'relative',
    width: '100vw',
    height: '1000vh',
    backgroundColor: blue[900],
    overflowX: 'hidden'
  },
  meassage: {
    position: 'fixed',
    top: 'calc(44vh)',
    color: '#fff',
    textAlign: 'center',
    width: '100%',
    fontWeight: 'bold'
  },
  title: {
    fontSize: '3.5rem'
  },
  placeholder: {
    fontSize: '1.1rem'
  },
  canvas: {
    position: 'fixed'
  }
})

class Caculating2 extends Component {
  constructor () {
    super()
    this.state = {
      timeline: {},
      eventList: ['scroll', 'touchmove'],
      squareList: [
        {
          color: pink[500],
          rotation: 35,
          size: GRAPH_SIZE.SQUARE * 1.2,
          from: {top: SIZE.FULL_PAGE.HEIGHT + 250, left: 0},
          to: {top: 250, left: SIZE.FULL_PAGE.WIDTH},
          position: 0
        },
        {
          color: blue[700],
          rotation: 45,
          size: GRAPH_SIZE.SQUARE,
          from: {top: SIZE.FULL_PAGE.HEIGHT + 150, left: 0},
          to: {top: 150, left: SIZE.FULL_PAGE.WIDTH},
          position: 0.5
        },
        {
          color: blue[700],
          rotation: 30,
          size: GRAPH_SIZE.SQUARE * 0.5,
          from: {top: SIZE.FULL_PAGE.HEIGHT - 150, left: 0},
          to: {top: -150, left: SIZE.FULL_PAGE.WIDTH},
          position: 0.6
        },
        {
          color: '#000',
          rotation: 45,
          size: GRAPH_SIZE.SQUARE * 1.2,
          from: {top: SIZE.FULL_PAGE.HEIGHT + 300, left: 0},
          to: {top: 300, left: SIZE.FULL_PAGE.WIDTH},
          position: 0.7
        },
        {
          color: '#000',
          rotation: 70,
          size: GRAPH_SIZE.SQUARE * 3,
          from: {top: SIZE.FULL_PAGE.HEIGHT - 100, left: 0},
          to: {top: -100, left: SIZE.FULL_PAGE.WIDTH},
          position: 1.2
        },
        {
          color: pink[500],
          rotation: 40,
          size: GRAPH_SIZE.SQUARE * 0.7,
          from: {top: SIZE.FULL_PAGE.HEIGHT + 400, left: 0},
          to: {top: 400, left: SIZE.FULL_PAGE.WIDTH},
          position: 1.5
        },
        {
          color: blue[700],
          rotation: 45,
          size: GRAPH_SIZE.SQUARE,
          from: {top: SIZE.FULL_PAGE.HEIGHT - 200, left: 0},
          to: {top: -200, left: SIZE.FULL_PAGE.WIDTH},
          position: 3
        },
        {
          color: pink[500],
          rotation: 35,
          size: GRAPH_SIZE.SQUARE,
          from: {top: SIZE.FULL_PAGE.HEIGHT + 100, left: 0},
          to: {top: 100, left: SIZE.FULL_PAGE.WIDTH},
          position: 3.5
        },
        {
          color: '#000',
          rotation: 45,
          size: GRAPH_SIZE.SQUARE * 1.5,
          from: {top: SIZE.FULL_PAGE.HEIGHT - 250, left: 0},
          to: {top: -250, left: SIZE.FULL_PAGE.WIDTH},
          position: 3.9
        },
        {
          color: pink[500],
          rotation: 35,
          size: GRAPH_SIZE.SQUARE * 0.5,
          from: {top: SIZE.FULL_PAGE.HEIGHT + 20, left: 0},
          to: {top: 20, left: SIZE.FULL_PAGE.WIDTH},
          position: 4.4
        }
      ],
      lastScrollTop: window.pageYOffset,
      start: false
    }
  }

  componentWillUnmount () {
    this.canceled = true
    const {eventList} = this.state
    eventList.forEach(event => window.removeEventListener(event, this.handleScroll, false))
  }

  componentDidMount () {
    const {eventList} = this.state

    eventList.forEach(event => window.addEventListener(event, this.handleScroll, false))

    const timeline = new TimelineMax({paused: true, onComplete: this.onFinish})

    this.state.squareList.forEach((info, index) => {
      const {color, rotation, size, from, to, position} = info
      from.left = -size * 1.5
      to.left += size * 1.5

      const square = createSquare({
        size,
        color,
        ref: this.refs[`square${index}`]
      })
      const squareAnimation = TweenLite.fromTo(square, 1, {
        rotation,
        ...from
      }, {
        ...to
      })
      timeline.add(squareAnimation, position)
    })
    const lastSquare = createSquare({
      size: GRAPH_SIZE.SQUARE * 20,
      color: pink[500],
      ref: this.refs.lastSquare
    })
    const squareAnimation = TweenLite.fromTo(lastSquare, 1, {
      rotation: 45,
      top: SIZE.FULL_PAGE.HEIGHT,
      left: GRAPH_SIZE.SQUARE * 20 * -1
    }, {
      top: SIZE.FULL_PAGE.HEIGHT * 2 * -1,
      left: -SIZE.FULL_PAGE.WIDTH - 300
    })
    timeline.add(squareAnimation)

    !this.canceled && this.setState({
      timeline,
      start: true
    })
  }

  // https://stackoverflow.com/a/31223774/5003128
  handleScroll = (event) => {
    const {start} = this.state

    if (!start) return

    const newScrollTop = window.pageYOffset || document.documentElement.scrollTop
    const newLastScrollTop = newScrollTop <= 0 ? 0 : newScrollTop
    !this.canceled && this.setState({lastScrollTop: newLastScrollTop})

    const {clientHeight, scrollHeight} = document.body
    const targetHeight = (scrollHeight - clientHeight)
    const progressPersent = newLastScrollTop / targetHeight
    this.state.timeline.progress(progressPersent)
  }

  onFinish = () => {
    const {updateThemeBgColor, updatePage} = this.props

    updateThemeBgColor(pink[500])
    updatePage({caculating2: true}, 'caculating3')
  }

  render () {
    const {classes} = this.props
    const {squareList} = this.state
    return (
      <section ref='page' className={classes.root}>
        {
          squareList.map((info, index) => (
            <canvas
              key={index}
              className={classes.canvas}
              ref={`square${index}`}
              width={info.size}
              height={info.size} />
          ))
        }
        <div className={classes.meassage}>
          <p className={classes.title}>NOW CACULATING</p>
          <p className={classes.placeholder}>Keep scrolling to show the result</p>
        </div>
        <canvas
          className={classes.canvas}
          ref='lastSquare'
          width={GRAPH_SIZE.SQUARE * 20}
          height={GRAPH_SIZE.SQUARE * 20} />
      </section>
    )
  }
}

export default withStyles(styles)(Caculating2)



