import React, {Component} from 'react'
import {withRouter} from 'react-router-dom'
import { withStyles } from '@material-ui/core/styles'

import blue from '@material-ui/core/colors/blue'

import Homepage from './Homepage/Homepage'
import Question1 from './Question/Question1'
import Question2 from './Question/Question2'
import Question3 from './Question/Question3'
import Caculating1 from './Caculating/Caculating1'
import Caculating2 from './Caculating/Caculating2'
import Caculating3 from './Caculating/Caculating3'
import Result from './Result/Result'

const styles = () => ({
  root: {
    width: '100vw',
    height: '100vh',
    transition: 'background-color 1s',
    backgroundColor: blue[700]
  }
})

class ParallaxContent extends Component {
  constructor () {
    super()
    this.state = {
      themeBgColor: '',
      isFinish: {
        homepage: true,
        question1: true,
        question2: true,
        question3: true,
        caculating1: true,
        caculating2: true,
        caculating3: true,
        result: false
      }
    }
  }

  updateThemeBgColor = (color) => this.setState({themeBgColor: color})

  updatePage = (pageState, nextPage) => {
    if (nextPage === 'result') {
      this.setState({
        isFinish: {
          ...this.state.isFinish,
          ...pageState
        }
      })
      setTimeout(() => {
        this.setState({
          isFinish: {
            ...this.state.isFinish,
            [nextPage]: false
          }
        })
        window.scrollTo(0, 0)
      }, 1000)
    } else {
      this.setState({
        isFinish: {
          ...this.state.isFinish,
          ...pageState,
          [nextPage]: false
        }
      })
      window.scrollTo(0, 0)
    }
  }

  tryAgain = () => {
    this.setState({
      ...this.state,
      themeBgColor: blue[700],
      isFinish: {
        homepage: false,
        question1: true,
        question2: true,
        question3: true,
        caculating1: true,
        caculating2: true,
        caculating3: true,
        result: true
      }
    })
  }

  render () {
    const {classes} = this.props
    const {isFinish, themeBgColor} = this.state
    const {
      homepage,
      question1,
      question2,
      question3,
      caculating1,
      caculating2,
      caculating3,
      result
    } = isFinish
    const pageProps = {
      updateThemeBgColor: this.updateThemeBgColor,
      updatePage: this.updatePage
    }

    return (
      <div className={classes.root} style={{backgroundColor: themeBgColor}}>
        {!homepage && <Homepage {...pageProps} />}
        {!question1 && <Question1 {...pageProps} />}
        {!question2 && <Question2 {...pageProps} />}
        {!question3 && <Question3 {...pageProps} />}
        {!caculating1 && <Caculating1 {...pageProps} />}
        {!caculating2 && <Caculating2 {...pageProps} />}
        {!caculating3 && <Caculating3 {...pageProps} />}
        {!result && <Result tryAgain={this.tryAgain} />}
      </div>
    )
  }
}

export default withRouter(withStyles(styles)(ParallaxContent))



