import React, {Component} from 'react'
import { withStyles } from '@material-ui/core/styles'
import {TimelineMax, TweenLite} from 'gsap'
import {SIZE, GRAPH_BASE_SIZE} from '../../../constant'
import {createAngle} from '../../../util/drawCanvasUtil'

import blue from '@material-ui/core/colors/blue'

const GRAPH_SIZE = {
  ANGLE: GRAPH_BASE_SIZE * 0.15,
  SQUARE: GRAPH_BASE_SIZE * 0.15
}

const styles = () => ({
  root: {
    position: 'relative',
    width: '100vw',
    height: '1000vh',
    backgroundColor: blue[700],
    overflowX: 'hidden'
  },
  meassage: {
    position: 'fixed',
    top: 'calc(44vh)',
    color: '#fff',
    textAlign: 'center',
    width: '100%',
    fontWeight: 'bold'
  },
  title: {
    fontSize: '3.5rem'
  },
  placeholder: {
    fontSize: '1.1rem'
  },
  canvas: {
    position: 'fixed'
  }
})

class Caculating1 extends Component {
  constructor () {
    super()
    this.state = {
      timeline: {},
      eventList: ['scroll', 'touchmove'],
      angleList: [
        {
          color: '#fff',
          rotation: 0,
          size: GRAPH_SIZE.ANGLE * 0.7,
          from: {top: SIZE.FULL_PAGE.HEIGHT + 200, left: 0},
          to: {top: 200, left: SIZE.FULL_PAGE.WIDTH},
          position: 0
        },
        {
          color: blue[900],
          rotation: 45,
          size: GRAPH_SIZE.ANGLE,
          from: {top: SIZE.FULL_PAGE.HEIGHT - 100, left: 0},
          to: {top: -100, left: SIZE.FULL_PAGE.WIDTH},
          position: 0
        },
        {
          color: '#000',
          rotation: 70,
          size: GRAPH_SIZE.ANGLE * 0.5,
          from: {top: SIZE.FULL_PAGE.HEIGHT, left: 0},
          to: {top: 0, left: SIZE.FULL_PAGE.WIDTH},
          position: 0.6
        },
        {
          color: '#000',
          rotation: 0,
          size: GRAPH_SIZE.ANGLE * 1.2,
          from: {top: SIZE.FULL_PAGE.HEIGHT, left: 0},
          to: {top: 0, left: SIZE.FULL_PAGE.WIDTH},
          position: 0.7
        },
        {
          color: blue[900],
          rotation: 100,
          size: GRAPH_SIZE.ANGLE * 3,
          from: {top: SIZE.FULL_PAGE.HEIGHT, left: 0},
          to: {top: 0, left: SIZE.FULL_PAGE.WIDTH},
          position: 1.2
        },
        {
          color: '#fff',
          rotation: 0,
          size: GRAPH_SIZE.ANGLE * 0.7,
          from: {top: SIZE.FULL_PAGE.HEIGHT - 200, left: 0},
          to: {top: -200, left: SIZE.FULL_PAGE.WIDTH},
          position: 2
        },
        {
          color: '#000',
          rotation: 20,
          size: GRAPH_SIZE.ANGLE * 0.6,
          from: {top: SIZE.FULL_PAGE.HEIGHT - 250, left: 0},
          to: {top: -250, left: SIZE.FULL_PAGE.WIDTH},
          position: 3
        },
        {
          color: blue[900],
          rotation: 45,
          size: GRAPH_SIZE.ANGLE,
          from: {top: SIZE.FULL_PAGE.HEIGHT, left: 0},
          to: {top: 0, left: SIZE.FULL_PAGE.WIDTH},
          position: 3.5
        },
        {
          color: '#000',
          rotation: 0,
          size: GRAPH_SIZE.ANGLE * 1.1,
          from: {top: SIZE.FULL_PAGE.HEIGHT + 250, left: 0},
          to: {top: 250, left: SIZE.FULL_PAGE.WIDTH},
          position: 3.9
        },
        {
          color: blue[900],
          rotation: 100,
          size: GRAPH_SIZE.ANGLE * 3.5,
          from: {top: SIZE.FULL_PAGE.HEIGHT + 200, left: 0},
          to: {top: 200, left: SIZE.FULL_PAGE.WIDTH},
          position: 5
        }
      ],
      lastScrollTop: window.pageYOffset,
      start: false
    }
  }

  componentWillUnmount () {
    this.canceled = true
    const {eventList} = this.state
    eventList.forEach(event => window.removeEventListener(event, this.handleScroll, false))
  }

  componentDidMount () {
    const {eventList} = this.state

    eventList.forEach(event => window.addEventListener(event, this.handleScroll, false))

    const timeline = new TimelineMax({paused: true, onComplete: this.onFinish})

    this.state.angleList.forEach((info, index) => {
      const {color, rotation, size, from, to, position} = info
      from.left = -size
      to.left += size

      const angle = createAngle({
        size,
        color,
        ref: this.refs[`angle${index}`]
      })
      const angleAnimation = TweenLite.fromTo(angle, 1, {
        rotation,
        ...from
      }, {
        ...to
      })
      timeline.add(angleAnimation, position)
    })
    const lastAngle = createAngle({
      size: GRAPH_SIZE.ANGLE * 25,
      color: blue[900],
      ref: this.refs.lastAngle
    })
    const angleAnimation = TweenLite.fromTo(lastAngle, 1, {
      rotation: 45,
      top: SIZE.FULL_PAGE.HEIGHT,
      left: GRAPH_SIZE.ANGLE * 30 * -1
    }, {
      top: SIZE.FULL_PAGE.HEIGHT * 2 * -1,
      left: -SIZE.FULL_PAGE.WIDTH - 300
    })
    timeline.add(angleAnimation)

    !this.canceled && this.setState({
      timeline,
      start: true
    })
  }

  // https://stackoverflow.com/a/31223774/5003128
  handleScroll = (event) => {
    const {start} = this.state

    if (!start) return

    const newScrollTop = window.pageYOffset || document.documentElement.scrollTop
    const newLastScrollTop = newScrollTop <= 0 ? 0 : newScrollTop
    !this.canceled && this.setState({lastScrollTop: newLastScrollTop})

    const {clientHeight, scrollHeight} = document.body
    const targetHeight = (scrollHeight - clientHeight)
    const progressPersent = newLastScrollTop / targetHeight
    this.state.timeline.progress(progressPersent)
  }

  onFinish = () => {
    const {updateThemeBgColor, updatePage} = this.props

    updateThemeBgColor(blue[900])
    updatePage({caculating1: true}, 'caculating2')
  }

  render () {
    const {classes} = this.props
    const {angleList} = this.state
    return (
      <section ref='page' className={classes.root}>
        {
          angleList.map((info, index) => (
            <canvas
              key={index}
              className={classes.canvas}
              ref={`angle${index}`}
              width={info.size}
              height={info.size} />
          ))
        }
        <div className={classes.meassage}>
          <p className={classes.title}>ALL DONE!</p>
          <p className={classes.placeholder}>Keep scrolling to show the result</p>
        </div>
        <canvas
          className={classes.canvas}
          ref='lastAngle'
          width={GRAPH_SIZE.ANGLE * 30}
          height={GRAPH_SIZE.ANGLE * 30} />
      </section>
    )
  }
}

export default withStyles(styles)(Caculating1)



