export const createAngle = ({ref, size, style = 'fill', color}) => {
  const angleCtx = ref.getContext('2d')

  angleCtx.moveTo(size / 2, 0)
  angleCtx.lineTo(0, size)
  angleCtx.lineTo(size, size)
  angleCtx.lineTo(size / 2, 0)
  angleCtx[`${style}Style`] = color
  angleCtx[style]()

  return ref
}

export const createSquare = ({ref, size, color}) => {
  const squareCtx = ref.getContext('2d')

  squareCtx.moveTo(0, 0)
  squareCtx.lineTo(size, 0)
  squareCtx.lineTo(size, size)
  squareCtx.lineTo(0, size)
  squareCtx.lineTo(0, 0)
  squareCtx.fillStyle = color
  squareCtx.fill()

  return ref
}

export const createCircle = ({ref, size, color}) => {
  const circleCtx = ref.getContext('2d')
  const radius = size / 2

  circleCtx.beginPath()
  circleCtx.arc(radius, radius, radius, 0, 2 * Math.PI)
  circleCtx.fillStyle = color
  circleCtx.fill()

  return ref
}

export const createStar = ({ref, size, color}) => {
  const starCtx = ref.getContext('2d')
  const starSize = size

  starCtx.moveTo(starSize / 2, 0)
  starCtx.lineTo(starSize * 0.1, starSize * 0.8)
  starCtx.lineTo(starSize * 0.9, starSize * 0.8)
  starCtx.lineTo(starSize / 2, 0)
  starCtx.fillStyle = color
  starCtx.fill()

  starCtx.moveTo(starSize / 2, starSize)
  starCtx.lineTo(starSize * 0.9, starSize * 0.2)
  starCtx.lineTo(starSize * 0.1, starSize * 0.2)
  starCtx.lineTo(starSize / 2, starSize)
  starCtx.fillStyle = color
  starCtx.fill()

  return ref
}

export const createDrawer = ({ref, size, color}) => {
  const drawerCtx = ref.getContext('2d')
  const drawerSize = size

  drawerCtx.moveTo(0, 0)
  drawerCtx.lineTo(drawerSize.WIDTH, 0)
  drawerCtx.lineTo(drawerSize.WIDTH, drawerSize.HEIGHT)
  drawerCtx.lineTo(0, drawerSize.HEIGHT)
  drawerCtx.lineTo(0, 0)
  drawerCtx.fillStyle = color
  drawerCtx.fill()

  return ref
}



