import React, {Component} from 'react'
import { withStyles } from '@material-ui/core/styles'
import {TimelineMax, TweenLite} from 'gsap'
import {SIZE, GRAPH_BASE_SIZE} from '../../../constant'
import {createCircle} from '../../../util/drawCanvasUtil'

import blue from '@material-ui/core/colors/blue'
import pink from '@material-ui/core/colors/pink'

const GRAPH_SIZE = {
  CIRCLE: GRAPH_BASE_SIZE * 0.15
}

const styles = () => ({
  root: {
    position: 'relative',
    width: '100vw',
    height: '1000vh',
    transition: 'all 1s',
    backgroundColor: pink[500],
    overflowX: 'hidden'
  },
  meassage: {
    position: 'fixed',
    top: 'calc(44vh)',
    color: '#000',
    textAlign: 'center',
    width: '100%',
    fontWeight: 'bold'
  },
  title: {
    fontSize: '3.5rem'
  },
  placeholder: {
    fontSize: '1.1rem'
  },
  canvas: {
    position: 'fixed'
  }
})

class Caculating3 extends Component {
  constructor () {
    super()
    this.state = {
      timeline: {},
      eventList: ['scroll', 'touchmove'],
      circleList: [
        {
          color: blue[900],
          size: GRAPH_SIZE.CIRCLE * 2,
          from: {top: SIZE.FULL_PAGE.HEIGHT - 450, left: 0},
          to: {top: -450, left: SIZE.FULL_PAGE.WIDTH},
          position: 0
        },
        {
          color: '#fff',
          size: GRAPH_SIZE.CIRCLE * 1.7,
          from: {top: SIZE.FULL_PAGE.HEIGHT + 50, left: 0},
          to: {top: 50, left: SIZE.FULL_PAGE.WIDTH},
          position: 0.7
        },
        {
          color: '#000',
          size: GRAPH_SIZE.CIRCLE * 1.5,
          from: {top: SIZE.FULL_PAGE.HEIGHT + 50, left: 0},
          to: {top: 50, left: SIZE.FULL_PAGE.WIDTH},
          position: 0.9
        },
        {
          color: blue[900],
          size: GRAPH_SIZE.CIRCLE * 1,
          from: {top: SIZE.FULL_PAGE.HEIGHT - 400, left: 0},
          to: {top: -400, left: SIZE.FULL_PAGE.WIDTH},
          position: 1.1
        },
        {
          color: blue[700],
          size: GRAPH_SIZE.CIRCLE * 0.8,
          from: {top: SIZE.FULL_PAGE.HEIGHT, left: 0},
          to: {top: 0, left: SIZE.FULL_PAGE.WIDTH},
          position: 1.5
        },
        {
          color: '#fff',
          size: GRAPH_SIZE.CIRCLE * 3,
          from: {top: SIZE.FULL_PAGE.HEIGHT - 300, left: 0},
          to: {top: -300, left: SIZE.FULL_PAGE.WIDTH},
          position: 2
        },
        {
          color: '#000',
          size: GRAPH_SIZE.CIRCLE * 1.8,
          from: {top: SIZE.FULL_PAGE.HEIGHT - 500, left: 0},
          to: {top: -500, left: SIZE.FULL_PAGE.WIDTH},
          position: 3
        },
        {
          color: blue[900],
          size: GRAPH_SIZE.CIRCLE * 1.8,
          from: {top: SIZE.FULL_PAGE.HEIGHT + 200, left: 0},
          to: {top: 200, left: SIZE.FULL_PAGE.WIDTH},
          position: 3
        },
        {
          color: blue[700],
          size: GRAPH_SIZE.CIRCLE * 0.7,
          from: {top: SIZE.FULL_PAGE.HEIGHT + 350, left: 0},
          to: {top: 350, left: SIZE.FULL_PAGE.WIDTH},
          position: 3.2
        }
      ],
      lastScrollTop: window.pageYOffset,
      start: false
    }
  }

  componentWillUnmount () {
    this.canceled = true
    const {eventList} = this.state
    eventList.forEach(event => window.removeEventListener(event, this.handleScroll, false))
  }

  componentDidMount () {
    const {updateThemeBgColor} = this.props
    const {eventList} = this.state

    updateThemeBgColor('#fff')
    eventList.forEach(event => window.addEventListener(event, this.handleScroll, false))

    const timeline = new TimelineMax({paused: true, onComplete: this.onFinish})

    this.state.circleList.forEach((info, index) => {
      const {color, size, from, to, position} = info
      from.left = -size * 1.5
      to.left += size * 1.5

      const circle = createCircle({
        size,
        color,
        ref: this.refs[`circle${index}`]
      })
      const circleAnimation = TweenLite.fromTo(circle, 1, {
        ...from
      }, {
        ...to
      })
      timeline.add(circleAnimation, position)
    })
    const lastCircle = createCircle({
      size: GRAPH_SIZE.CIRCLE * 20,
      color: '#fff',
      ref: this.refs.lastCircle
    })
    const circleAnimation = TweenLite.fromTo(lastCircle, 1, {
      top: SIZE.FULL_PAGE.HEIGHT,
      left: GRAPH_SIZE.CIRCLE * 20 * -1
    }, {
      top: SIZE.FULL_PAGE.HEIGHT * 2 * -1,
      left: -SIZE.FULL_PAGE.WIDTH - 300
    })
    timeline.add(circleAnimation)

    !this.canceled && this.setState({
      timeline,
      start: true
    })
  }

  // https://stackoverflow.com/a/31223774/5003128
  handleScroll = (event) => {
    const {start} = this.state

    if (!start) return

    const newScrollTop = window.pageYOffset || document.documentElement.scrollTop
    const newLastScrollTop = newScrollTop <= 0 ? 0 : newScrollTop
    const {clientHeight, scrollHeight} = document.body
    const targetHeight = (scrollHeight - clientHeight)
    const progressPersent = newLastScrollTop / targetHeight

    !this.canceled && this.setState({lastScrollTop: newLastScrollTop})
    this.state.timeline.progress(progressPersent)
  }

  onFinish = () => {
    const {updateThemeBgColor, updatePage} = this.props

    updateThemeBgColor(blue[700])
    updatePage({caculating3: true}, 'result')
  }

  render () {
    const {classes} = this.props
    const {circleList} = this.state
    return (
      <section ref='page' className={classes.root}>
        {
          circleList.map((info, index) => (
            <canvas
              key={index}
              className={classes.canvas}
              ref={`circle${index}`}
              width={info.size}
              height={info.size} />
          ))
        }
        <div className={classes.meassage}>
          <p className={classes.title}>YOU ARE…</p>
          <p className={classes.placeholder}>Keep scrolling to show the result</p>
        </div>
        <canvas
          className={classes.canvas}
          ref='lastCircle'
          width={GRAPH_SIZE.CIRCLE * 20}
          height={GRAPH_SIZE.CIRCLE * 20} />
      </section>
    )
  }
}

export default withStyles(styles)(Caculating3)



