import React, {Component} from 'react'
import { withStyles } from '@material-ui/core/styles'
import {Linear} from 'gsap'
import {SIZE, GRAPH_BASE_SIZE} from '../../../constant'
import {
  createDrawer,
  createAngle,
  createCircle,
  createSquare
} from '../../../util/drawCanvasUtil'
import {
  createFadeInAnimation,
  createMoveInAnimation,
  createSamePositionRepeatAnimation
} from '../../../util/animationUtil'

import blue from '@material-ui/core/colors/blue'
import pink from '@material-ui/core/colors/pink'

const DURATION = {
  CONTENT: 1,
  DRAWER: 1,
  GRAPH: 1
}

const GRAPH_SIZE = {
  SQUARE: GRAPH_BASE_SIZE * 0.15,
  ANGLE: GRAPH_BASE_SIZE * 0.1,
  CIRCLE: GRAPH_BASE_SIZE * 0.3
}

const styles = () => ({
  root: {
    position: 'relative',
    width: '100vw',
    height: '100vh',
    backgroundColor: blue[700],
    overflow: 'hidden'
  },
  canvas: {
    position: 'absolute'
  },
  contentArea: {
    display: 'inline-block',
    position: 'absolute',
    left: '10vw',
    top: '10vw',
    width: '35vw'
  },
  quest1: {
    color: pink[500],
    fontWeight: 'bold',
    fontSize: '3rem'
  },
  title: {
    color: '#fff',
    fontWeight: 'bold',
    fontSize: '1.8rem',
    marginBottom: '.5rem'
  },
  placeholder: {
    color: blue[900],
    fontSize: '1.2rem'
  },
  option: {
    display: 'inline-block',
    width: '100%',
    textAlign: 'center',
    color: '#fff',
    padding: '.5rem',
    border: '2px solid #fff',
    borderRadius: '2rem',
    marginBottom: '.5rem',
    cursor: 'pointer',
    '&:hover': {
      backgroundColor: '#fff',
      color: blue[500]
    }
  }
})

class Question1 extends Component {
  constructor () {
    super()
    this.state = {
      optionList: [
        'Destroy all humans in the world',
        'Make the war never happened',
        'You won’t get old and live forever'
      ],
      infiniteAnimationList: [],
      hideFullContent: false
    }
  }

  componentWillUnmount () {
    this.state.infiniteAnimationList.forEach(animation => animation.kill())
  }

  componentDidMount () {
    const {content} = this.refs

    createFadeInAnimation({
      target: content,
      duration: DURATION.CONTENT,
      from: {opacity: 0},
      to: {opacity: 1},
      save: this.saveAnimationToState
    })
      .then(() => {
        if (this.state.hideFullContent) return

        const {drawer} = this.refs
        createDrawer({
          ref: drawer,
          size: SIZE.FULL_PAGE,
          color: blue[900]
        })
        const moveAnimation = createMoveInAnimation({
          target: drawer,
          duration: DURATION.DRAWER,
          from: {x: SIZE.FULL_PAGE.WIDTH, y: 0},
          to: {x: SIZE.FULL_PAGE.WIDTH * 0.6, y: 0},
          save: this.saveAnimationToState
        })

        return moveAnimation
      })
      .then(() => {
        if (this.state.hideFullContent) return
        const {angle, square, circle} = this.refs

        createAngle({
          ref: angle,
          size: GRAPH_SIZE.ANGLE,
          color: '#fff'
        })
        createSquare({
          ref: square,
          size: GRAPH_SIZE.SQUARE,
          color: pink[500]
        })
        createCircle({
          ref: circle,
          size: GRAPH_SIZE.CIRCLE,
          color: '#000'
        })
        const horizontalPosition = SIZE.FULL_PAGE.WIDTH
        const verticalPosition = SIZE.FULL_PAGE.HEIGHT
        const position = {
          square: {
            from: {x: horizontalPosition * 0.65, y: verticalPosition},
            to: {x: horizontalPosition * 0.65, y: verticalPosition * 0.2}
          },
          angle: {
            from: {x: horizontalPosition * 0.92, y: verticalPosition + 100},
            to: {x: horizontalPosition * 0.92, y: verticalPosition * 0.4}
          },
          circle: {
            from: {x: horizontalPosition * 0.8, y: verticalPosition + 200},
            to: {x: horizontalPosition * 0.8, y: verticalPosition * 0.8}
          }
        }
        const squareAnimation = createMoveInAnimation({
          target: square,
          duration: DURATION.GRAPH,
          ...position.square,
          save: this.saveAnimationToState
        })
        const angleAnimation = createMoveInAnimation({
          target: angle,
          duration: DURATION.GRAPH,
          ...position.angle,
          save: this.saveAnimationToState
        })
        const circleAnimation = createMoveInAnimation({
          target: circle,
          duration: DURATION.GRAPH,
          ...position.circle,
          save: this.saveAnimationToState
        })

        return Promise.all([
          squareAnimation,
          angleAnimation,
          circleAnimation
        ])
      })
      .then(() => {
        if (this.state.hideFullContent) return

        const {square, angle, circle} = this.refs
        const horizontalPosition = SIZE.FULL_PAGE.WIDTH

        createSamePositionRepeatAnimation({
          target: [square, angle],
          duration: DURATION.GRAPH * 3,
          otherProps: {
            rotation: 360,
            transformOrigin: 'center',
            ease: Linear.easeNone
          },
          save: this.saveAnimationToState
        })
        createSamePositionRepeatAnimation({
          target: circle,
          duration: DURATION.GRAPH * 5,
          otherProps: {
            bezier: {
              curviness: 1.5,
              values: [
                {x: horizontalPosition * 0.8},
                {x: horizontalPosition * 0.75},
                {x: horizontalPosition * 0.8}
              ]
            },
            ease: Linear.easeNone
          },
          save: this.saveAnimationToState
        })
      })
  }

  saveAnimationToState = (animation) => {
    this.setState({infiniteAnimationList: [
      ...this.state.infiniteAnimationList,
      animation
    ]})
  }

  selectOption = () => {
    const {updateThemeBgColor, updatePage} = this.props
    const {drawer} = this.refs

    this.setState({hideFullContent: true})
    updateThemeBgColor(blue[900])
    createMoveInAnimation({
      target: drawer,
      duration: DURATION.DRAWER * 0.1,
      from: {x: SIZE.FULL_PAGE.WIDTH * 0.6},
      to: {x: 0},
      save: this.saveAnimationToState
    })
      .then(() => {
        updatePage({question1: true}, 'question2')
      })
  }

  render () {
    const {classes} = this.props
    const {optionList, hideFullContent} = this.state

    return (
      <section className={classes.root}>
        {
          !hideFullContent &&
          <div ref='content' className={classes.contentArea}>
            <p className={classes.quest1}>Q1</p>
            <p className={classes.title}>
              If one day you get a gift that can change the rule of the world, which ability will you choose?
            </p>
            <p className={classes.placeholder}>
              Choose the answer appeared in your mind instantly
            </p>
            <br />
            {
              optionList.map((option, index) => (
                <p
                  key={index}
                  className={classes.option}
                  onClick={this.selectOption}>{option}</p>
              ))
            }
          </div>
        }
        <canvas
          className={classes.canvas}
          ref='drawer'
          width={SIZE.FULL_PAGE.WIDTH}
          height={SIZE.FULL_PAGE.HEIGHT} />
        {
          !hideFullContent &&
          <div>
            <canvas
              className={classes.canvas}
              ref='square'
              width={GRAPH_SIZE.SQUARE}
              height={GRAPH_SIZE.SQUARE} />
            <canvas
              className={classes.canvas}
              ref='angle'
              width={GRAPH_SIZE.ANGLE}
              height={GRAPH_SIZE.ANGLE} />
            <canvas
              className={classes.canvas}
              ref='circle'
              width={GRAPH_SIZE.CIRCLE}
              height={GRAPH_SIZE.CIRCLE} />
          </div>
        }
      </section>
    )
  }
}

export default withStyles(styles)(Question1)



